.input {
    margin: 2rem auto;
    padding: 1rem;
    width: 90%;
    max-width: 40rem;
  }
  
  .input label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .input input {
    font: inherit;
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.15rem;
    margin-bottom: 0.5rem;
  }
  
  .input input:focus {
    outline: none;
    border-color: #4f005f;
  }

  
  .user_mode {
    margin-right: 10px;
    font: inherit;
    border: 1px solid #4f005f;
    background: #4f005f;
    color: white;
    cursor: pointer;
}

.user_mode:hover,
.user_mode:active {
  background: #741188;
  border-color: #741188;
}